import React from "react"
import { Link } from "gatsby"
import Jumbotron from "react-bootstrap/Jumbotron"
import Container from "react-bootstrap/Container"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Button from "react-bootstrap/Button"
import Image from "react-bootstrap/Image"

import Layout from "../layouts/_main"

import portrait from "../images/portrait.jpg"
import SocialMedia from "../components/socialMedia"
import SEO from "../components/seo"

class Index extends React.Component {

  render() {
    return (
      <Layout>
        <SEO titleTemplate="%s - Home" />
        <Jumbotron fluid id="heroBanner" className="mb-0 bg-transparent text-light heroImage heroImage3">
          <div className="container-fluid text-center">
            <h1 className="display-3">Hi,</h1>
            <h1 className="display-3">I'm Sam!</h1>
            <p className="lead">Game Programmer <span className="px-2">&#8226;</span> Full-Stack Web Developer</p>
            <h3><SocialMedia /></h3>
          </div>
        </Jumbotron>
        <Container id="intro">
          <Row noGutters>
            <Col sm className="p-2">
              <Image id="portrait" fluid thumbnail roundedCircle className="d-block bg-light mx-auto" src={portrait} alt="Portrait" />
            </Col>
            <Col sm="8" className="p-2">
              <h2 className="mt-0 text-justify text-center text-sm-left">Who am I?</h2>
              <p className="lead text-justify text-center text-sm-left">
                Hi! I'm Sam Chancey aka Rorazoro. I am a Full-Stack Web Developer working to get into the Game Industry as a Game Programmer.
                <br />
                I am very passionate for video games and work towards my dream of making game development my full-time career.
                <br />
                Check out my latest projects on my <Link to="/portfolio">portfolio page</Link>.
                <br />
                Also if you have any questions, feel free to <Link to="/contact">contact me</Link>.
              </p>
              {/* <Button variant="dark" as={Link} to="/about">Read More</Button> */}
            </Col>
          </Row>
        </Container>

      </Layout>
    )
  }
}

export default Index